import React, { useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import axios from "axios";
import "../styles/RegiserStyles.css";
import { Form, Input, message } from "antd";
import Reset from "../image/reset.jpg";

const ResetPassword = () => {
  const [newPassword, setNewPassword] = useState("");
  const [confirmNewPassword, setConfirmNewPassword] = useState("");
  const navigate = useNavigate();
  const location = useLocation();
  const { otp } = location.state;

  const handleResetPassword = async (e) => {
    try {
      const response = await axios.post("/api/v1/user/resetPassword", {
        otp,
        newPassword,
        confirmNewPassword,
      });
      message.success(response.data.message);
      navigate("/login");
    } catch (error) {
      message.error(error.response.data.message || "Something went wrong");
    }
  };

  return (
    <>
      <div className="form-container ">
        <div className="container">
          <div className="img-container">
            <img src={Reset} alt="Sign-up-image" className="img" />
          </div>

          <Form
            layout="vertical"
            onFinish={handleResetPassword}
            className="register-form"
          >
            <h2 className="head">Reset Password</h2>
            <Form.Item
              className="form-label"
              name="password"
              rules={[
                { required: true },
                {
                  pattern:
                    /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{6,}$/,
                  message:
                    "Password must be at least 6 characters long, include uppercase and lowercase letters, a number, and a special character.",
                },
              ]}
            >
              <Input
                type="password"
                placeholder="New Password"
                value={newPassword}
                onChange={(e) => setNewPassword(e.target.value)}
                className="form-input"
              />
            </Form.Item>

            <Form.Item
              className="form-label"
              name="confirmPassword"
              rules={[
                { required: true, message: "Please confirm your password" },
              ]}
            >
              <Input
                type="password"
                placeholder="Confirm New Password"
                value={confirmNewPassword}
                onChange={(e) => setConfirmNewPassword(e.target.value)}
                className="form-input"
              />
            </Form.Item>
            <button type="submit" className="btn">
              Reset Password
            </button>
          </Form>
        </div>
      </div>
    </>
  );
};

export default ResetPassword;
