import "../styles/RegiserStyles.css";
import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import axios from "axios";
import OTP from "../image/otp.png";
import { Form, message } from "antd";

const VerifyOtp = () => {
  const [otp, setOtp] = useState(["", "", "", "", "", ""]);
  const [resendTimer, setResendTimer] = useState(0);
  const navigate = useNavigate();
  const location = useLocation();
  const { email } = location.state;

  const handleInputChange = (value, index) => {
    if (value.length > 1) return; // Ensure single digit
    const newOtp = [...otp];
    newOtp[index] = value;
    setOtp(newOtp);

    if (value && index < 5) {
      const nextInput = document.getElementById(`otp-input-${index + 1}`);
      nextInput && nextInput.focus();
    }
  };

  const handleVerify = async () => {
    try {
      const otpValue = otp.join(""); // Combine all boxes into one string
      const response = await axios.post("/api/v1/user/verifyotp", {
        email,
        otp: otpValue,
      });
      message.success(response.data.message);
      navigate("/resetPassword", { state: { email, otp: otpValue } });
    } catch (error) {
      message.error(error.response?.data?.message || "Invalid OTP");
    }
  };

  const handleResend = async () => {
    try {
      await axios.post("/api/v1/user/resendOtp", { email });
      message.success("OTP has been resent successfully!");
      setOtp(["", "", "", "", "", ""]); // Clear previous OTP inputs
      setResendTimer(60); // Start 60-second cooldown
    } catch (error) {
      message.error(error.response?.data?.message || "Failed to resend OTP");
    }
  };

  useEffect(() => {
    if (resendTimer > 0) {
      const timer = setInterval(() => {
        setResendTimer((prev) => {
          if (prev <= 1) {
            clearInterval(timer);
            return 0;
          }
          return prev - 1;
        });
      }, 1000);
      return () => clearInterval(timer);
    }
  }, [resendTimer]);

  return (
    <>
      <div className="form-container">
        <div className="container">
          <div className="img-container">
            <img src={OTP} alt="OTP_image" className="img" />
          </div>
          <Form
            onFinish={handleVerify}
            layout="vertical"
            className="register-form"
          >
            <h2 className="head">Verify OTP</h2>
            <div className="otp-box">
              {otp.map((digit, index) => (
                <input
                  key={index}
                  id={`otp-input-${index}`}
                  type="text"
                  value={digit}
                  onChange={(e) => handleInputChange(e.target.value, index)}
                  maxLength="1"
                  className="otp-input"
                />
              ))}
            </div>
            <button type="submit" className="btn">
              Verify OTP
            </button>

            <div className="resend-container">
              <p>Didn't receive the code?</p>
              <button
                className="resend-btn"
                onClick={handleResend}
                disabled={resendTimer > 0}
              >
                Resend OTP
              </button>
              {resendTimer > 0 && (
                <p className="timer">Resend in {resendTimer}s</p>
              )}
            </div>
          </Form>
        </div>
      </div>
    </>
  );
};

export default VerifyOtp;
