import React from "react";
import "../styles/RegiserStyles.css";
import { Form, Input, message } from "antd";
import { useDispatch } from "react-redux";
import { showLoading, hideLoading } from "../redux/features/alertSlice";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import loginImg from "../image/image.png";

const Login = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  //form handler
  const onfinishHandler = async (values) => {
    try {
      dispatch(showLoading());
      const res = await axios.post("/api/v1/user/login", values);
      window.location.reload();
      dispatch(hideLoading());
      if (res.data.success) {
        localStorage.setItem("token", res.data.token);
        message.success("Login Successfully");
        navigate("/");
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      dispatch(hideLoading());
      if (error.response && error.response.status === 401) {
        message.error("Invalid Email or Password"); // Custom error for wrong password
      } else {
        message.error("User Not Found");
      }
      console.log(error);
    }
  };
  return (
    <div className="form-container ">
      <div className="container">
        <div className="img-container">
          <img src={loginImg} alt="loginimage" className="img" />
        </div>
        <Form
          layout="vertical"
          onFinish={onfinishHandler}
          className="login-form "
        >
          <h3 className="head">Sign in</h3>
          <div className="item">
            <Form.Item name="email" className="form-label">
              <Input
                type="email"
                required
                className="form-input"
                autoFocus
                placeholder="Email address"
              />
            </Form.Item>
            <Form.Item name="password" className="form-label">
              <Input
                type="password"
                required
                placeholder="Password"
                className="form-input"
              />
            </Form.Item>
          </div>
          <button className="btn btn-primary" type="submit">
            Login
          </button>
          <br />
          <Link to="/forgotPassword">
            <p className="fg">Forgot password?</p>
          </Link>
          <div className="box1">
            <p>Don't have an account?</p>
            <Link to="/register" className="text">
              Register here
            </Link>
          </div>
        </Form>
      </div>
    </div>
  );
};

export default Login;
