import React from "react";
import "../styles/RegiserStyles.css";
import { Form, Input, message } from "antd";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { showLoading, hideLoading } from "../redux/features/alertSlice";
import signUp from "../image/sign-up.jpg";

const Register = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  //form handler
  const onfinishHandler = async (values) => {
    try {
      if (values.password !== values.confirmPassword) {
        return message.error("Passwords do not match");
      }
      dispatch(showLoading());
      const res = await axios.post("/api/v1/user/register", values);
      dispatch(hideLoading());
      if (res.data.success) {
        message.success("Register Successfully!");
        navigate("/login");
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      dispatch(hideLoading());
      console.log(error);
      message.error("Something Went Wrong");
    }
  };
  return (
    <>
      <div className="form-container ">
        <div className="container container1">
          <div className="img-container">
            <img src={signUp} alt="Sign-up-image" className="img" />
          </div>

          <Form
            layout="vertical"
            onFinish={onfinishHandler}
            className="register-form"
          >
            <h2 className="head">Sign Up</h2>
            <Form.Item name="name" className="form-label">
              <Input
                type="text"
                placeholder="Name"
                className="form-input"
                required
              />
            </Form.Item>
            <Form.Item
              name="email"
              className="form-label"
              rules={[
                {
                  required: true,
                  type: "email",
                  message: "Enter a valid email",
                },
              ]}
            >
              <Input placeholder="Email address" className="form-input" />
            </Form.Item>

            <Form.Item
              className="form-label"
              name="password"
              rules={[
                { required: true },
                {
                  pattern:
                    /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{6,}$/,
                  message:
                    "Password must be at least 6 characters long, include uppercase and lowercase letters, a number, and a special character.",
                },
              ]}
            >
              <Input.Password placeholder="Password" className="form-input" />
            </Form.Item>
            <Form.Item
              className="form-label"
              name="confirmPassword"
              rules={[
                { required: true, message: "Please confirm your password" },
              ]}
            >
              <Input.Password
                placeholder="Confirm Password"
                className="form-input"
              />
            </Form.Item>
            <button className="btn btn-primary" type="submit">
              Register
            </button>

            <div className="box1">
              <p className="Lg"> Already have Account?</p>
              <Link to="/login" className="text Lg">
                Login here
              </Link>
            </div>
          </Form>
        </div>
      </div>
    </>
  );
};

export default Register;
